.goods {
  margin-bottom: 30px;

  @media (min-width: $tablet-width) {
    margin-bottom: 53px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 57px;
  }
}
.goods__list {
  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
  }
}
.goods__item {
  margin-bottom: 30px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    position: relative;
    margin-bottom: 50px;
  }

  @media (min-width: $desktop-width) {
    width: 316px;
  }
}
.goods__img-wrapper {

  @media (min-width: $tablet-width) {
    position: absolute;
    top: 0;
    left: 0;
  }

  @media (min-width: $desktop-width) {
    position: static;
    width: auto;
    height: auto;
  }
}
.goods__img {
  display: block;
  object-fit: cover;
  width: 260px;
  height: 195px;

  @media (min-width: $tablet-width) {
    width: 334px;
    height: 334px;
  }

  @media (min-width: $desktop-width) {
    width: 316px;
    height: 462px;
  }
}

.goods__content {
  padding: 30px 28px;
  border: solid 1px #ececec;

  &:hover {
    box-shadow: 0px 6px 10px 0 rgba(#000000, 0.1);
  }

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    width: 335px;
    min-height: 334px;
    margin-left: auto;
  }

  @media (min-width: $desktop-width) {
    width: auto;
    min-height: initial;
  }
}

.goods__header {
  margin-bottom: 5px;
  font-size: 17px;
  font-weight: bold;
  line-height: 24px;
  color: #000;

  @media (min-width: $tablet-width) {
    font-size: 20px;
  }
}
.goods__desk {
  margin-bottom: 20px;
  font-size: 13px;
  line-height: 30px;

  @media (min-width: $tablet-width) {
    font-size: 15px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 40px;
  }
}

.goods__bottom {
  position: relative;
  border-top: 1px solid #ececec;
  padding-top: 20px;

  @media (min-width: $tablet-width) {
    margin-top: auto;
  }
}

.goods__price {
  font-size: 15px;
  font-weight: bold;
  line-height: 24px;
  color: #000;

  @media (min-width: $tablet-width) {
    font-size: 17px;
  }
}

.goods__link {
  position: absolute;
  bottom: 5px;
  right: 0;
  cursor: pointer;
}

.goods__icon {
  width: 20px;
  height: 19px;
  opacity: 0.2;

  .goods__link:hover & {
    opacity: 1;
    fill: #63d1bb;
  }

  .goods__link:active & {
    opacity: 0.3;
    fill: #63d1bb;
  }
}
