.btn {
  display: block;
  height: 52px;
  padding: 0 20px;
  font-family: $font-stack-default;
  font-size: 18px;
  font-weight: bold;
  line-height: 52px;
  text-align: center;
  color: #444;
  text-transform: uppercase;
  border: solid 2px #444;
  cursor: pointer;

  &:hover {
    color: #ffffff;
    background-color: #424242;
  }

  &:active {
    color: rgba(#ffffff, 0.3);
  }
}
