.toggle {
  position: absolute;

  .main-nav--closed & {
    width: 20px;
    height: 14px;
  }

  .main-nav--opened & {
    width: 20px;
    height: 21px;
  }
}

.toggle__line {
  position: absolute;

  transition: all 0.2s;

  .main-nav--closed & {
    right: 0;
    top: 0;
    height: 2px;
    width: 100%;

    background-color: #000000;

    &:nth-child(2) {
      top: 6px;
    }

    &:nth-child(3) {
      top: 12px;
    }
  }

  .main-nav--opened & {
    top: 50%;
    left: 50%;

    height: 2px;
    width: 26px;

    transform: translate(-50%, -50%) rotate(-45deg);

    background-color: #000000;

    &:nth-child(2) {
      transform: translate(-50%, -50%) rotate(45deg);
    }

    &:nth-child(3) {
      opacity: 0;
    }
  }
}
