/* open-sans-regular - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Open Sans"), local("OpenSans"), url("../fonts/open-sans-v29-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/open-sans-v29-latin_cyrillic-regular.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* open-sans-700 - latin_cyrillic */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Open Sans"), local("OpenSans"), url("../fonts/open-sans-v29-latin_cyrillic-700.woff2") format("woff2"), url("../fonts/open-sans-v29-latin_cyrillic-700.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
html {
  background-color: #ffffff;
}

body {
  min-width: 320px;
  max-width: none;
  margin: 0 auto;
  font-size: 17px;
  line-height: 30px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #444444;
  font-weight: 400;
  font-style: normal;
  background-color: #ffffff;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
}

.container {
  width: 260px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 668px;
  }
}
@media (min-width: 1150px) {
  .container {
    width: 1050px;
  }
  .container--section {
    padding-left: 175px;
  }
}

.section-header {
  font-size: 25px;
  font-weight: bold;
  line-height: 30px;
  color: #000;
}
.section-header--inner {
  padding: 70px 30px;
  background-color: #f1f1f1;
  margin-bottom: 28px;
}
@media (min-width: 768px) {
  .section-header {
    font-size: 43px;
    line-height: 50px;
  }
  .section-header--inner {
    padding-top: 95px;
    padding-bottom: 85px;
    margin-bottom: 77px;
    background-image: url("../img/zigzag-fill.svg");
    background-position: center bottom -12px;
    background-size: 768px;
    background-repeat: repeat-x;
  }
}
@media (min-width: 1150px) {
  .section-header {
    font-size: 53px;
    line-height: 57px;
  }
  .section-header--inner {
    padding-top: 97px;
    padding-bottom: 101px;
    margin-bottom: 126px;
    background-image: url("../img/zigzag-fill.svg");
    background-position: center bottom -12px;
    background-size: 1050px;
    background-repeat: repeat-x;
  }
}
.section-header--big {
  text-align: center;
}
@media (max-width: 767px) {
  .section-header--big {
    font-size: 27px;
    color: #010000;
  }
}

.btn {
  display: block;
  height: 52px;
  padding: 0 20px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 18px;
  font-weight: bold;
  line-height: 52px;
  text-align: center;
  color: #444;
  text-transform: uppercase;
  border: solid 2px #444;
  cursor: pointer;
}
.btn:hover {
  color: #ffffff;
  background-color: #424242;
}
.btn:active {
  color: rgba(255, 255, 255, 0.3);
}

.slider {
  overflow: hidden;
}

.slider__item {
  flex-shrink: 0;
}

.slider__arrows {
  position: relative;
  height: 52px;
  border: 1px solid #ececec;
}
@media (min-width: 768px) {
  .slider__arrows {
    height: 26px;
    border: none;
  }
}

.slider__arrow {
  display: block;
  position: absolute;
  top: 0;
  left: auto;
  width: 50%;
  height: 52px;
  margin-top: initial;
  background-position: 38px center;
  background-size: 50px 26px;
  background-repeat: no-repeat;
}
.slider__arrow:after {
  display: none;
}
.slider__arrow--left {
  background-image: url(../img/arrow-left.svg);
  border-right: 1px solid #ececec;
}
.slider__arrow--right {
  left: 50%;
  background-image: url(../img/arrow-right.svg);
}
.slider__arrow:hover {
  opacity: 0.6;
}
.slider__arrow:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .slider__arrow {
    height: 26px;
    border: none;
  }
  .slider__arrow--left {
    background-position: left center;
  }
  .slider__arrow--right {
    background-position: right center;
  }
}

.modal {
  display: none;
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  overflow: hidden;
}
.modal--show {
  display: block;
}

.modal__inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
}

.main-nav {
  position: relative;
  z-index: 1000;
  height: 80px;
  font-size: 17px;
  font-weight: bold;
  line-height: 24px;
  color: #000;
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .main-nav {
    height: 89px;
  }
}
@media (min-width: 1150px) {
  .main-nav {
    width: 1150px;
    margin: 0 auto;
  }
}

.main-nav__logo {
  position: absolute;
  top: 22px;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .main-nav__logo {
    left: 50px;
    transform: none;
  }
}
@media (min-width: 1150px) {
  .main-nav__logo {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.main-nav__toggle {
  right: 30px;
  top: 50%;
  transform: translateY(-50%);
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__menu-wrapper {
  animation-name: menuAnim;
  animation-duration: 0.3s;
}
@media (max-width: 767px) {
  .main-nav--opened .main-nav__menu-wrapper {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    margin-top: 1px;
    background-color: #ffffff;
  }
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__menu-wrapper {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav__menu-wrapper {
    display: flex;
    flex-wrap: wrap;
  }
}

@keyframes menuAnim {
  0% {
    transform: translateX(-1000px);
  }
  100% {
    transform: translateX(0px);
  }
}
.main-nav__list--3 {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__list--1 {
    display: flex;
  }
  .main-nav__list--2 {
    display: flex;
    order: -1;
    width: 100%;
    margin-bottom: 18px;
  }
}
@media (min-width: 1150px) {
  .main-nav__list--1 {
    width: 50%;
    margin-bottom: 18px;
  }
  .main-nav__list--2 {
    width: 50%;
    order: 0;
  }
  .main-nav__list--3 {
    display: flex;
  }
}

@media (max-width: 767px) {
  .main-nav__item {
    border-bottom: 1px solid #ececec;
  }
}
@media (min-width: 768px) {
  .main-nav__item--search {
    margin-left: auto;
    border-left: 1px solid #ececec;
    border-right: 1px solid #ececec;
  }
  .main-nav__item--search span {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}

.main-nav__item-link {
  display: block;
  position: relative;
  height: 79px;
  padding: 0px 0px 0px 76px;
}
.main-nav__item-link:hover {
  opacity: 0.6;
}
.main-nav__item-link:active {
  opacity: 0.3;
}
.main-nav__item-link--2:hover {
  opacity: 1;
}
.main-nav__item-link--2:hover span {
  opacity: 0.6;
}
.main-nav__item-link--2:active {
  opacity: 0.3;
}
@media (max-width: 767px) {
  .main-nav__item-link {
    width: 320px;
    margin: 0 auto;
    line-height: 79px;
  }
}
@media (min-width: 768px) {
  .main-nav__item-link {
    height: auto;
    padding: 0;
  }
  .main-nav__item-link--1 {
    font-size: 15px;
    padding-right: 37px;
  }
  .main-nav__item-link--2 {
    height: 88px;
    line-height: 88px;
  }
  .main-nav__item-link--search {
    width: 82px;
  }
  .main-nav__item-link--cart {
    padding-left: 83px;
  }
}
@media (min-width: 1150px) {
  .main-nav__item-link--1 {
    height: 88px;
    font-size: 17px;
    line-height: 88px;
    padding: 0px 35px 0px 35px;
  }
  .main-nav__item:first-child .main-nav__item-link--1 {
    padding-left: 0;
    border-right: 1px solid #ececec;
  }
  .main-nav__item-link--3 {
    font-size: 15px;
    padding-right: 60px;
  }
}

.main-nav__icon {
  position: absolute;
  left: 30px;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0.2;
}
.main-nav__item-link:hover .main-nav__icon {
  opacity: 1;
  fill: #63d1bb;
}
@media (min-width: 768px) {
  .main-nav__icon--search {
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.main-nav__hint {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__hint {
    display: block;
    margin-left: auto;
    font-size: 15px;
    font-weight: 400;
    color: #444;
  }
}

.logo__img {
  width: 86px;
  height: 35px;
}
.logo__img:hover {
  opacity: 0.6;
}
.logo__img:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .logo__img {
    width: 112px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .logo__img {
    width: 149px;
    height: 72px;
  }
}

.toggle {
  position: absolute;
}
.main-nav--closed .toggle {
  width: 20px;
  height: 14px;
}
.main-nav--opened .toggle {
  width: 20px;
  height: 21px;
}

.toggle__line {
  position: absolute;
  transition: all 0.2s;
}
.main-nav--closed .toggle__line {
  right: 0;
  top: 0;
  height: 2px;
  width: 100%;
  background-color: #000000;
}
.main-nav--closed .toggle__line:nth-child(2) {
  top: 6px;
}
.main-nav--closed .toggle__line:nth-child(3) {
  top: 12px;
}
.main-nav--opened .toggle__line {
  top: 50%;
  left: 50%;
  height: 2px;
  width: 26px;
  transform: translate(-50%, -50%) rotate(-45deg);
  background-color: #000000;
}
.main-nav--opened .toggle__line:nth-child(2) {
  transform: translate(-50%, -50%) rotate(45deg);
}
.main-nav--opened .toggle__line:nth-child(3) {
  opacity: 0;
}

.categories {
  padding-top: 70px;
  margin-bottom: 26px;
  background: #f9f9f9 url(../img/hero-mobile.jpg) center -17px/auto no-repeat scroll padding-box border-box;
}
@media (min-width: 768px) {
  .categories {
    position: relative;
    padding-top: 155px;
    margin-bottom: 50px;
    background-image: url(../img/hero.jpg);
    background-position: center top;
  }
  .categories::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 900;
    width: 100%;
    height: 18px;
    background: url(../img/zigzag-fill.svg) center bottom -12px/768px repeat-x;
  }
}
@media (min-width: 1150px) {
  .categories::after {
    background-size: 1150px;
    background-position: center bottom -16px;
  }
}

@media (max-width: 767px) {
  .categories__container {
    width: auto;
  }
}

.categories__header {
  width: 260px;
  margin: 0px auto 122px auto;
}
@media (min-width: 768px) {
  .categories__header {
    width: 500px;
    margin-bottom: 371px;
  }
  .categories__header br {
    display: none;
  }
}
@media (min-width: 1150px) {
  .categories__header {
    width: 620px;
    margin-bottom: 357px;
  }
}

.categories__list {
  font-size: 19px;
  font-weight: bold;
  line-height: 24px;
  color: #fff;
}
@media (min-width: 768px) {
  .categories__list {
    display: flex;
    font-size: 20px;
    line-height: 30px;
  }
}

.categories__item {
  padding: 27px 0px 28px 0px;
}
.categories__item:nth-child(2n-1) {
  background-color: #63d1bb;
}
.categories__item:nth-child(2n) {
  background-color: #56c3b2;
}
@media (min-width: 768px) {
  .categories__item {
    position: relative;
    width: 50%;
    padding: 30px 35px;
  }
}

.categories__link {
  display: block;
}
.categories__link:active {
  opacity: 0.3;
}
@media (max-width: 767px) {
  .categories__link {
    position: relative;
    width: 320px;
    padding: 0 30px;
    margin: 0 auto;
  }
}

.categories__icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0.5;
  fill: #149080;
}
.categories__icon--interior {
  right: 36px;
}
.categories__icon--toy {
  right: 27px;
}
.categories__link:hover .categories__icon {
  opacity: 0.7;
}
@media (min-width: 768px) {
  .categories__icon--toy {
    right: 34px;
  }
}

