    width: 334px;
    height: 334px;
  }
}
@media (min-width: 1150px) {
  .goods__img {
    width: 316px;
    height: 462px;
  }
}

.goods__content {
  padding: 30px 28px;
  border: solid 1px #ececec;
}
.goods__content:hover {
  box-shadow: 0px 6px 10px 0 rgba(0, 0, 0, 0.1);
}
@media (min-width: 768px) {
  .goods__content {
    display: flex;
    flex-direction: column;
    width: 335px;
    min-height: 334px;
    margin-left: auto;
  }
}
@media (min-width: 1150px) {
  .goods__content {
    width: auto;
    min-height: initial;
  }
}

.goods__header {
  margin-bottom: 5px;
  font-size: 17px;
  font-weight: bold;
  line-height: 24px;
  color: #000;
}
@media (min-width: 768px) {
  .goods__header {
    font-size: 20px;
  }
}

.goods__desk {
  margin-bottom: 20px;
  font-size: 13px;
  line-height: 30px;
}
@media (min-width: 768px) {
  .goods__desk {
    font-size: 15px;
  }
}
@media (min-width: 1150px) {
  .goods__desk {
    margin-bottom: 40px;
  }
}

.goods__bottom {
  position: relative;
  border-top: 1px solid #ececec;
  padding-top: 20px;
}
@media (min-width: 768px) {
  .goods__bottom {
    margin-top: auto;
  }
}

.goods__price {
  font-size: 15px;
  font-weight: bold;
  line-height: 24px;
  color: #000;
}
@media (min-width: 768px) {
  .goods__price {
    font-size: 17px;
  }
}

.goods__link {
  position: absolute;
  bottom: 5px;
  right: 0;
  cursor: pointer;
}

.goods__icon {
  width: 20px;
  height: 19px;
  opacity: 0.2;
}
.goods__link:hover .goods__icon {
  opacity: 1;
  fill: #63d1bb;
}
.goods__link:active .goods__icon {
  opacity: 0.3;
  fill: #63d1bb;
}

.manufacture {
  margin-bottom: 58px;
}
@media (min-width: 768px) {
  .manufacture {
    margin-bottom: 63px;
  }
}

#player {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}

.manufacture__container {
  padding: 25px 0px 30px 0px;
  border: solid 1px #ececec;
}
@media (min-width: 768px) {
  .manufacture__container {
    position: relative;
    padding-top: 490px;
    padding-bottom: 45px;
  }
}
@media (min-width: 768px) {
  .manufacture__container {
    padding-top: 95px;
    padding-bottom: 60px;
    padding-right: 55px;
  }
}
@media (min-width: 1150px) {
  .manufacture__container {
    min-height: 457px;
  }
}

.manufacture__title {
  position: relative;
  width: 192px;
  padding-left: 55px;
  margin: 0 auto;
  margin-bottom: 20px;
  font-size: 17px;
  font-weight: bold;
  line-height: 24px;
  color: #000;
  margin-bottom: 20px;
}
.manufacture__title::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: 32px;
  height: 22px;
  transform: translateY(-50%);
  background: url(../img/video-icon.svg) center/32px no-repeat;
}
@media (min-width: 768px) {
  .manufacture__title {
    width: 584px;
    margin-bottom: 35px;
    font-size: 20px;
  }
}
@media (min-width: 1150px) {
  .manufacture__title {
    width: 230px;
    padding-left: 0;
    margin-right: 0;
  }
  .manufacture__title::before {
    top: initial;
    bottom: 100%;
    margin-bottom: 30px;
    transform: none;
  }
}

.manufacture__video-wrapper {
  position: relative;
  margin-bottom: 30px;
  width: 260px;
  height: 150px;
}
@media (min-width: 768px) {
  .manufacture__video-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    margin-bottom: 0;
    height: 455px;
  }
}
@media (min-width: 1150px) {
  .manufacture__video-wrapper {
    width: 668px;
    height: 455px;
  }
}

.manufacture__img {
  display: block;
  width: 260px;
  height: 150px;
  object-fit: cover;
}
@media (min-width: 768px) {
  .manufacture__img {
    width: 668px;
    height: 455px;
  }
}

.manufacture__play {
  cursor: pointer;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 101px;
  height: 77px;
}
.manufacture__play:hover {
  transform: translate(-50%, -50%) scale(1.2);
}
.manufacture__play:active {
  transform: translate(-50%, -50%) scale(1);
  opacity: 0.3;
}

.manufacture__desk {
  font-size: 13px;
  line-height: 30px;
  width: 192px;
  margin: 0 auto;
  margin-bottom: 25px;
}
@media (min-width: 768px) {
  .manufacture__desk {
    width: 584px;
    margin-bottom: 35px;
    font-size: 15px;
  }
}
@media (min-width: 1150px) {
  .manufacture__desk {
    width: 230px;
    margin-right: 0;
  }
}

.manufacture__btn {
  width: 192px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .manufacture__btn {
    width: 584px;
  }
}
@media (min-width: 1150px) {
  .manufacture__btn {
    width: 230px;
    margin-right: 0;
  }
}