.week-goods {
  padding-bottom: 42px;
  margin-bottom: 30px;
  background: url(../img/zigzag-line.svg) center bottom / 768px repeat-x scroll padding-box border-box;

  @media (min-width: $tablet-width) {
    padding-bottom: 69px;
    margin-bottom: 55px;
  }

  @media (min-width: $desktop-width) {
    background-repeat: no-repeat;
    background-size: 1050px;
  }
}

.week-goods__container {
  position: relative;
  z-index: 1;

  @media (min-width: $desktop-width) {
    padding-top: 60px;
  }
}

.week-goods__header {
  margin-bottom: 7px;

  @media (min-width: $tablet-width) {
    margin-bottom: 50px;
  }
}

.week-goods__tag {
  margin-bottom: 25px;
  font-size: 17px;
  font-weight: bold;
  line-height: 20px;
  color: #63d1bb;

  @media (min-width: $tablet-width) {
    position: absolute;
    top: 10px;
    right: 28px;
    width: 75px;
    text-align: center
  }

  @media (min-width: $desktop-width) {
    top: 75px;
    left: 50px;
  }
}

.week-goods__inner {
  @media (min-width: $tablet-width) and (max-width: $desktop-width - 1) {
    position: relative;
  }

  @media (min-width: $tablet-width) {
    width: 100%;
    padding-right: 376px;
  }

  @media (min-width: $desktop-width) {
    padding-right: 583px;
  }
}

.week-goods__text-wrapper { }

.week-goods__description {
  margin-bottom: 25px;
  font-size: 15px;
  line-height: 24px;

  @media (min-width: $tablet-width) {
    margin-bottom: 40px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 65px;
  }
}

.week-goods__table {
  table-layout: fixed;
  width: 100%;
  margin-bottom: 28px;
  font-size: 15px;
  line-height: 20px;

  col {
    &:nth-child(1) {
      width: 98px;
    }
  }

  tr {
    border-bottom: 1px solid #eceded;
  }

  td, th {
    padding: 10px 30px 10px 0px;
    font-weight: normal;

    &:last-child {
      padding-right: 0;
    }
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 70px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 110px;
  }
}
.week-goods__img-wrapper {
  margin-bottom: 27px;

  @media (min-width: $tablet-width) {
    position: absolute;
    right: 0;
    top: 6px;
  }

  @media (min-width: $desktop-width) {
    z-index: -1;
  }
}
.week-goods__price {
  margin-bottom: 25px;
  font-weight: bold;
  line-height: 24px;
  text-align: center;

  @media (min-width: $tablet-width) {
    margin-bottom: 30px;
    font-size: 20px;
  }
}
.week-goods__order {

}
.week-goods__img {
  @media (min-width: $tablet-width) {
    width: 333px;
    height: auto;
  }

  @media (min-width: $desktop-width) {
    width: 525px;
  }
}
