.about-us {
  margin-bottom: 55px;
}
.about-us__header {
  margin-bottom: 30px;

  @media (min-width: $tablet-width) {
    margin-bottom: 45px;
  }
}

.about-us__list {
  font-size: 17px;
  font-weight: bold;
  line-height: 20px;
  color: #000;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
  }

  @media (min-width: $desktop-width) {
    width: 235px + 115px + 295px;
  }
}
.about-us__item {
  position: relative;
  padding-top: 50px;
  margin-bottom: 30px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    width: 50%;

    &:nth-child(2n-1) {
      padding-right: 100px;
    }
  }

  @media (min-width: $desktop-width) {
    &:nth-child(2n-1) {
      padding-right: 115px;
    }
  }
}
.about-us__icon {
  position: absolute;
  top: 0;
  left: 0;
  fill: #63d1bb;
}
