.categories {
  padding-top: 70px;
  margin-bottom: 26px;
  background: #f9f9f9 url(../img/hero-mobile.jpg) center -17px / auto no-repeat scroll padding-box border-box;

  @media (min-width: $tablet-width) {
    position: relative;
    padding-top: 155px;
    margin-bottom: 50px;
    background-image: url(../img/hero.jpg);
    background-position: center top;

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 900;
      width: 100%;
      height: 18px;

      background: url(../img/zigzag-fill.svg) center bottom -12px / 768px repeat-x;
    }
  }

  @media (min-width: $desktop-width) {
    &::after {
      background-size: 1150px;
      background-position: center bottom -16px;
    }
  }
}

.categories__container {
  @media (max-width: $mobile-only) {
    width: auto;
  }
}

.categories__header {
  width: 260px;
  margin: 0px auto 122px auto;

  @media (min-width: $tablet-width) {
    width: 500px;
    margin-bottom: 371px;

    br {
      display: none;
    }
  }

  @media (min-width: $desktop-width) {
    width: 620px;
    margin-bottom: 357px;
  }
}

.categories__list {
  font-size: 19px;
  font-weight: bold;
  line-height: 24px;
  color: #fff;

  @media (min-width: $tablet-width) {
    display: flex;
    font-size: 20px;
    line-height: 30px;
  }
}
.categories__item {
  padding: 27px 0px 28px 0px;

  &:nth-child(2n-1) {
    background-color: #63d1bb;
  }

  &:nth-child(2n) {
    background-color: #56c3b2;
  }

  @media (min-width: $tablet-width) {
    position: relative;
    width: 50%;
    padding: 30px 35px;
  }
}

.categories__link {
  display: block;

  &:active {
    opacity: 0.3;
  }

  @media (max-width: $mobile-only) {
    position: relative;
    width: 320px;
    padding: 0 30px;
    margin: 0 auto;
  }
}
.categories__icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0.5;
  fill: #149080;

  &--interior {
    right: 36px;
  }

  &--toy {
    right: 27px;
  }

  .categories__link:hover & {
    opacity: 0.7;
  }

  @media (min-width: $tablet-width) {
    &--toy {
      right: 34px;
    }
  }
}
