.main-nav {
  position: relative;
  z-index: 1000;
  height: 80px;

  font-size: 17px;
  font-weight: bold;
  line-height: 24px;
  color: #000;

  border-bottom: 1px solid #ececec;

  @media (min-width: $tablet-width) {
    height: 89px;
  }

  @media (min-width: $desktop-width) {
    width: 1150px;
    margin: 0 auto;
  }
}

.main-nav__logo {
  position: absolute;
  top: 22px;
  left: 50%;
  transform: translateX(-50%);

  @media (min-width: $tablet-width) {
    left: 50px;
    transform: none;
  }

  @media (min-width: $desktop-width) {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.main-nav__toggle {
  right: 30px;
  top: 50%;
  transform: translateY(-50%);

  @media (min-width: $tablet-width) {
    display: none;
  }
}


.main-nav__menu-wrapper {
  animation-name: menuAnim;
  animation-duration:  0.3s;
  .main-nav--opened & {
    @media (max-width: $mobile-only) {
      position: absolute;
      top: 100%;
      left: 0;

      width: 100%;
      margin-top: 1px;

      background-color: #ffffff;
    }
  }

  .main-nav--closed & {
    @media (max-width: $mobile-only) {
      display: none;
    }
  }

  @media (min-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
  }
}

@keyframes menuAnim {
  0% { transform: translateX(-1000px);}
  100% { transform: translateX(0px);}
}

.main-nav__list {
  &--3 {
    display: none;
  }

  @media (min-width: $tablet-width) {

    &--1 {
      display: flex;
    }

    &--2 {
      display: flex;
      order: -1;

      width: 100%;
      margin-bottom: 18px;
    }
  }

  @media (min-width: $desktop-width) {
    &--1 {
      width: 50%;
      margin-bottom: 18px;
    }

    &--2 {
      width: 50%;
      order: 0;
    }

    &--3 {
      display: flex;
    }
  }
}

.main-nav__item {
  @media (max-width: $mobile-only) {
    border-bottom: 1px solid #ececec;
  }

  @media (min-width: $tablet-width) {
    &--search  {
      margin-left: auto;
      border-left: 1px solid #ececec;
      border-right: 1px solid #ececec;

      span {
        @include visually-hidden;
      }
    }
  }
}

.main-nav__item-link {
  display: block;
  position: relative;
  height: 79px;
  padding: 0px 0px 0px 76px;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  &--2:hover {
    opacity: 1;

    span {
      opacity: 0.6;
    }
  }

  &--2:active {
    opacity: 0.3;
  }

  @media (max-width: $mobile-only) {
    width: 320px;
    margin: 0 auto;
    line-height: 79px;
  }

  @media (min-width: $tablet-width) {
    height: auto;
    padding: 0;

    &--1 {
      font-size: 15px;
      padding-right: 37px;
    }

    &--2 {
      height: 88px;
      line-height: 88px;
    }

    &--search {
      width: 82px;
    }

    &--cart {
      padding-left: 83px;
    }
  }

  @media (min-width: $desktop-width) {
    &--1 {
      height: 88px;
      font-size: 17px;
      line-height: 88px;
      padding: 0px 35px 0px 35px;

      .main-nav__item:first-child & {
        padding-left: 0;
        border-right: 1px solid #ececec;
      }
    }

    &--3 {
      font-size: 15px;
      padding-right: 60px;
    }
  }
}

.main-nav__icon {
  position: absolute;
  left: 30px;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0.2;

  .main-nav__item-link:hover & {
    opacity: 1;
    fill: #63d1bb;
  }

  @media (min-width: $tablet-width) {

    &--search {
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}

.main-nav__hint {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
    margin-left: auto;
    font-size: 15px;
    font-weight: 400;
    color: #444;
  }
}




