.section-header {
  font-size: 25px;
  font-weight: bold;
  line-height: 30px;
  color: #000;

  &--inner {
    padding: 70px 30px;
    background-color: #f1f1f1;
    margin-bottom: 28px;
  }

  @media (min-width: $tablet-width) {
    font-size: 43px;
    line-height: 50px;

    &--inner {
      padding-top: 95px;
      padding-bottom: 85px;
      margin-bottom: 77px;
      background-image: url('../img/zigzag-fill.svg');
      background-position:center bottom -12px;
      background-size: 768px;
      background-repeat: repeat-x;
    }
  }

  @media (min-width: $desktop-width) {
    font-size: 53px;
    line-height: 57px;

    &--inner {
      padding-top: 97px;
      padding-bottom: 101px;
      margin-bottom: 126px;
      background-image: url('../img/zigzag-fill.svg');
      background-position:center bottom -12px;
      background-size: 1050px;
      background-repeat: repeat-x;
    }
  }

  &--big {
    text-align: center;

    @media (max-width: $mobile-only) {
      font-size: 27px;
      color: #010000;
    }

  }
}
