.copyright {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 180px;
  margin: 0 auto;
  cursor: pointer;

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    margin: 0;
  }
}
.copyright__text {
  font-size: 19px;
  font-weight: bold;
  line-height: 18px;
  color: #000;
}
.copyright__link {
}
.copyright__icon {
  fill: #63d1bb;

  .copyright:hover & {
    fill: #46c1ae;
  }
}
