.social {
  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 100%;
  padding: 0px 70px 27px 70px;
  margin: 0px auto 22px auto;

  border-bottom: 1px solid #e6e6e6;

  @media (min-width: $tablet-width) {
    width: 150px;
    padding: 0;
    margin: 0;
    margin-left: 65px;
    border-bottom: none;
  }
}
.social__item { }
.social__link {
  display: block;
}
.social__icon {
  fill: #000000;

  .social__link:hover & {
    fill: #46c1ae;
  }

  .social__link:active & {
    opacity: 0.3;
  }
}
