.reviews {
  position: relative;
  padding-bottom: 51px;
  margin-bottom: 40px;
  background: url('../img/zigzag-line.svg') center bottom / 768px repeat-x scroll padding-box border-box;

  @media (min-width: $tablet-width) {
    padding-top: 79px;
    padding-bottom: 130px;
    margin-bottom: 55px;
    background-image: url('../img/zigzag-line.svg'), url('../../img/review-bg.jpg');
    background-repeat: repeat-x, no-repeat;
    background-position: center bottom, left top;
    background-size: 768px, 350px;
  }

  @media (min-width: $desktop-width) {
    width: 1150px;
    padding-bottom: 160px;
    margin: 0 auto;
    margin-bottom: 75px;
    background-repeat: no-repeat;
    background-position: center bottom, left 50px top;
    background-size: 1050px, 350px;
  }
}

.reviews__container {
  position: relative;
}

.reviews__header {
  margin-bottom: 30px;

  @media (min-width: $tablet-width) {
    margin-bottom: 45px;
  }
}
.reviews__wrapper {
  margin-bottom: 38px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }
}
.reviews__list {
  margin-bottom: 30px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }
}
.reviews__item {
  width: 260px;

  @media (min-width: $tablet-width) {
    width: 668px;
  }

  @media (min-width: $desktop-width) {
    width: 875px;
  }
}
.reviews__text {
  margin-bottom: 30px;
  font-size: 15px;

  @media (min-width: $tablet-width) {
    margin-bottom: 55px;
    font-size: 17px;
    line-height: 30px;
  }

  @media (min-width: $desktop-width) {
    width: 772px;
  }
}
.reviews__author-info {
  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }
}
.reviews__author-full-name {
  display: block;
  font-weight: bold;
  line-height: 24px;
  color: #000;

  @media (min-width: $tablet-width) {
    margin-bottom: 5px;
    font-size: 18px;
  }
}
.reviews__author-nickname {
  display: block;
}

.reviews__arrows {
  @media (min-width: $tablet-width) {
    position: absolute;
    right: 19px;
    bottom: 15px;
    width: 137px;
  }
}
.reviews__btn {
  width: 100%;

  @media (min-width: $tablet-width) {
    width: 175px;
    position: absolute;
    top: 0;
    right: 0;
  }
}
