
.contacts {
  margin-bottom: 39px;

  @media (min-width: $tablet-width) {
    margin-bottom: 57px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 105px;
    overflow: hidden;
  }
}

.contacts__container {
  @media (min-width: $desktop-width) {
    position: relative;
    z-index: 1;
    padding-top: 75px;
  }
}

.contacts__header {
  position: relative;

  @media (max-width: $mobile-only) {
    margin-bottom: 40px;
    font-size: 18px;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 55px;

    &::before {
      content: '';
      position: absolute;
      right: 0;
      top: 50%;

      height: 81px;
      width: 47px;

      transform: translateY(-50%);

      background: url(../img/flag.svg) center / 47px no-repeat;
    }
  }

  @media (min-width: $desktop-width) {
    &::before {
      left: -100px;
      top: 50%;
    }
  }
}

.contacts__info {
  margin-bottom: 40px;
  font-size: 15px;

  @media (min-width: $tablet-width) {
    display: flex;
    margin-bottom: 55px;
  }

  @media (min-width: $desktop-width) {
    flex-wrap: wrap;
    width: 330px;
    margin-bottom: 80px;
  }
}
.contacts__item {
  display: flex;
  align-items: start;
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    width: 50%;
    padding-right: 30px;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
  }
}
.contact__title {
  padding-right: 32px;
}
.contacts__description {
  width: 180px;

  &--mail {
    border-bottom: 1px solid #62d2bb;
    cursor: pointer;

    &:hover {
      color: #62d2bb;
    }

    &:active {
      opacity: 0.3;
    }
  }

  @media (min-width: $tablet-width) {
    width: auto;
  }
}
.contacts__map {
  position: relative;
  width: 320px;
  margin-bottom: 39px;
  margin-left: -30px;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    background: url(../../img/map-pin.svg) 137px 145px/ 67px no-repeat;
  }

  @media (min-width: $tablet-width) {
    width: 768px;
    margin-bottom: 55px;
    margin-left: -50px;

    &::before {
      background-position: 363px 139px;
    }
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    width: 525px;

    &::before {
      background-position: 239px 146px;
    }
  }
}

#map {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
}

.contacts__map-img {
  width: 100%;
}
.contacts__btn {
  width: 260px;
  margin: 0 auto;

  @media (min-width: $desktop-width) {
    margin-left: 0;
  }
}
