.add-to-cart {
  width: 320px;
  padding: 40px 30px;
  box-shadow: 0px 5px 10px 0 #00010140;
  border-top: solid 1px #c6c6c6;
  border-bottom: solid 1px #c6c6c6;

  @media (min-width: $tablet-width) {
    width: 566px;
    padding: 45px 105px;
    border: 1px solid #c6c6c6;
  }
}

.add-to-cart__header {
  margin-bottom: 10px;
  text-align: center;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    margin-bottom: 25px;
    font-size: 30px;
    line-height: 57px;

    & br {
      display: none;
    }
  }
}

.add-to-cart__text {
  margin-bottom: 25px;
  font-size: 15px;
  line-height: 30px;
  text-align: center;
  color: #444;
  text-align: center;

  @media (min-width: $tablet-width) {
    font-size: 17px;
  }
}

.add-to-cart__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
}

.add-to-cart__item-btn {
  height: 52px;
  width: 82px;
  text-align: center;
  font-size: 17px;
  font-weight: bold;
  line-height: 48px;
  text-align: center;
  border: solid 2px rgba(#444, 0.1);
  background-color: #fff;

  &--active {
    border: solid 2px #63d1bb;
  }

  @media (min-width: $tablet-width) {
    width: 112px;
  }
}

.add-to-cart__add-btn {
  width: 100%;
  font-size: 17px;
  text-align: center;
  color: #ffffff;
  background-color: #63d1bb;
  border: none;
}
