.manufacture {
  margin-bottom: 58px;

  @media (min-width: $tablet-width) {
    margin-bottom: 63px;
  }
}

#player {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}

.manufacture__container {
  padding: 25px 0px 30px 0px;

  border: solid 1px #ececec;

  @media (min-width: $tablet-width) {
    position: relative;
    padding-top: 490px;
    padding-bottom: 45px;
  }

  @media (min-width: $tablet-width) {
    padding-top: 95px;
    padding-bottom: 60px;
    padding-right: 55px;
  }

  @media (min-width: $desktop-width) {
    min-height: 457px;
  }
}

.manufacture__title {
  position: relative;
  width: 192px;
  padding-left: 55px;
  margin: 0 auto;
  margin-bottom: 20px;
  font-size: 17px;
  font-weight: bold;
  line-height: 24px;
  color: #000;
  margin-bottom: 20px;

  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    width: 32px;
    height: 22px;
    transform: translateY(-50%);
    background:  url(../img/video-icon.svg) center / 32px no-repeat;
  }

  @media (min-width: $tablet-width) {
    width: 584px;
    margin-bottom: 35px;
    font-size: 20px;
  }

  @media (min-width: $desktop-width) {
    width: 230px;
    padding-left: 0;
    margin-right: 0;

    &::before {
      top: initial;
      bottom: 100%;
      margin-bottom: 30px;
      transform: none;
    }
  }
}

.manufacture__video-wrapper {
  position: relative;
  margin-bottom: 30px;
  width: 260px;
  height: 150px;

  @media (min-width: $tablet-width) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    margin-bottom: 0;
    height: 455px;
  }

  @media (min-width: $desktop-width) {
    width: 668px;
    height: 455px;
  }
}

.manufacture__img {
  display: block;
  width: 260px;
  height: 150px;
  object-fit: cover;

  @media (min-width: $tablet-width) {
    width: 668px;
    height: 455px;
  }
}

.manufacture__play {
  cursor: pointer;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 101px;
  height: 77px;

  &:hover {
    transform: translate(-50%, -50%) scale(1.2);
  }

  &:active {
    transform: translate(-50%, -50%) scale(1);
    opacity: 0.3;
  }
}

.manufacture__desk {
  font-size: 13px;
  line-height: 30px;
  width: 192px;
  margin: 0 auto;
  margin-bottom: 25px;

  @media (min-width: $tablet-width) {
    width: 584px;
    margin-bottom: 35px;
    font-size: 15px;
  }

  @media (min-width: $desktop-width) {
    width: 230px;
    margin-right: 0;
  }
}

.manufacture__btn {
  width: 192px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    width: 584px;
  }

  @media (min-width: $desktop-width) {
    width: 230px;
    margin-right: 0;
  }
}
