html {
  background-color: #ffffff;
}

body {
  min-width: $mobile-width;
  max-width: none;
  margin: 0 auto;

  font-size: 17px;
  line-height: 30px;
  font-family: $font-stack-default;
  color: $font-color;
  font-weight: 400;
  font-style: normal;

  background-color: $bg-color;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
}
