.page-footer {
  padding: 25px 0;
  background-color: #f1f1f1;

  @media (min-width: $tablet-width) {
    padding: 50px 0;
  }
}

.page-footer__container {
  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.page-footer__logo {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
  }
}

.page-footer__logo-img {
  width: 102px;
}
