.slider {
  overflow: hidden;
}

.slider__list {
}

.slider__item {
  flex-shrink: 0;
}

.slider__arrows {
  position: relative;
  height: 52px;
  border: 1px solid #ececec;

  @media (min-width: $tablet-width) {
    height: 26px;
    border: none;
  }
}

.slider__arrow {
  display: block;
  position: absolute;
  top: 0;
  left: auto;

  width: 50%;
  height: 52px;
  margin-top: initial;

  background-position: 38px center;
  background-size: 50px 26px;
  background-repeat: no-repeat;

  &:after {
    display: none;
  }

  &--left {
    background-image: url(../img/arrow-left.svg);
    border-right: 1px solid #ececec;
  }

  &--right {
    left: 50%;
    background-image: url(../img/arrow-right.svg);
  }

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    height: 26px;
    border: none;

    &--left {
      background-position: left center;
    }

    &--right {
      background-position: right center;
    }
  }
}
