.order {
  margin-bottom: 55px;
}
.order__desc {
  width: 260px;
  padding-right: 10px;
  margin: 0 auto;
  margin-bottom: 25px;
  font-size: 13px;
  line-height: 24px;

  @media (min-width: $tablet-width) {
    width: 668px;
    padding-right: 50px;
    font-size: 17px;
    line-height: 30px;
  }

  @media (min-width: $desktop-width) {
    width: 700px;
  }
}

.order__section {
  padding: 15px 0;
  font-size: 17px;
  font-weight: bold;
  line-height: 18px;
  text-align: left;
  color: #000;

  @media (min-width: $tablet-width) {
    padding: 50px 0;
    font-size: 19px;

    &:nth-child(2n + 1) {
      background-color: #f1f1f1;
    }
  }
}

.order__container {
  position: relative;
}

.order__title {
  display: flex;
  align-items: center;
  margin-bottom: 25px;
  font-weight: bold;
  line-height: 16px;
  color: #63d1bb;
  text-transform: uppercase;

  &--contacts {
    margin-bottom: 10px;
  }

  &::after {
    content: '';
    flex-grow: 1;
    height: 1px;
    background-color: #63d1bb;
  }

  @media (min-width: $tablet-width) {
    position: absolute;
    top: 15px;
    left: 0;

    &--full-name {
      top: 25px;
    }

    &--contacts {
      top: 25px;
    }

    &::after {
      display: none;
    }
  }

  @media (min-width: $desktop-width) {
    left: 65px;
  }
}

.order__title-inner {
  position: relative;
  padding-right: 25px;
}

.order__box-list {
  @media (min-width: $tablet-width) {
    width: 500px;
    margin-left: auto;

    &--color {
      display: flex;
      flex-wrap: wrap;
    }
  }

  @media (min-width: $desktop-width) {
    width: 700px;
    margin: 0 auto;
  }
}

.order__box-item {
  position: relative;
  padding: 11px 0;
  padding-left: 60px;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    padding: 17px 0;
    padding-left: 80px;
    margin-bottom: 23px;

    &--color {
      width: 50%;
      padding-right: 20px;

      &:nth-last-child(-n+2) {
        margin-bottom: 0;
      }
    }
  }
}

.order__box-label {
}

.order__box-indicator {
  position: absolute;
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  border: solid 2px #444;

  .order__box-item:hover &{
    border-color: #62d1ba;
  }

  .order__box-item:active &{
    border-color: rgba(#444, 0.3);
  }

  &--radio {
    border-radius: 50%;

    .order__radio:checked + &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 16px;
      height: 16px;
      background-color: #62d1ba;
      transform: translate(-50%, -50%);
      border-radius: 50%;
    }
  }

  @media (min-width: $tablet-width) {
    width: 52px;
    height: 52px;

    .order__radio:checked + &::before {
      width: 22px;
      height: 22px;
    }
  }
}

.order__box-indicator--checkbox {
  .order__checkbox:checked + & {
    background: url(../../img/icon-checkbox.svg) center / 22px no-repeat;
  }

  @media (min-width: $tablet-width) {
    .order__checkbox:checked + & {
      background-size: 33px;
    }
  }
}

.order__input-group {
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    position: relative;
    display: flex;
    align-items: baseline;
    width: 500px;
    margin-left: auto;

    &--contacts {
      width: auto;
    }
  }

  @media (min-width: $desktop-width) {
    width: 700px;
    margin-left: auto;
    margin-right: auto;
  }

  &--contacts {
    width: auto;
  }
}

.order__label {
  padding-right: 25px;
}

.order__input-wrapper {

  @media (min-width: $tablet-width) {
    position: relative;
    width: 500px;
    margin-left: auto;
  }

  @media (min-width: $desktop-width) {
    width: 700px;
    margin-right: auto;
  }
}

.order__input  {
  min-width: 0;
  width: 100%;
  padding: 17px 0;
  font-weight: normal;
  outline: none;
  border-bottom: 2px solid #000;

  .order__input-group:hover &,
  .order__input-group:active &,
  .order__input-group:focus-within & {
    border-color: #62d1ba;
  }

  &::placeholder {
    font-size: 17px;
    line-height: 18px;
    color: rgba(#444444, 0.3);
  }

  @media (min-width: $tablet-width) {
    &::placeholder {
      font-size: 19px;
    }
  }
}

.order__input-icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  fill: #000;
  opacity: 0.2;

  .order__input-group:hover &,
  .order__input-group:active &,
  .order__input-group:focus-within & {
    fill: #62d1ba;
    opacity: 1;
  }

  &--phone {
    right: 8px;
  }

  &--mail {
    right: 4px;
  }
}

.order__textarea {
  min-width: 0;
  width: 100%;
  padding: 18px;
  font-weight: normal;
  border: solid 2px #444;
  outline: none;

  .order__title:hover + &,
  &:hover,
  &:active,
  &:focus {
    border-color: #62d1ba;
  }

  &::placeholder {
    font-size: 17px;
    line-height: 18px;
    color: rgba(#444444, 0.3);
  }

  @media (min-width: $tablet-width) {
    display: block;
    width: 500px;
    margin-left: auto;

    &::placeholder {
      font-size: 19px;
    }
  }

  @media (min-width: $desktop-width) {
    width: 700px;
    margin-right: auto;
  }
}

.order__bottom {
  display: flex;
  flex-direction: column;
  width: 260px;
  margin: 0 auto;
  margin-top: 5px;

  @media (min-width: $tablet-width) {
    width: 668px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  @media (min-width: $desktop-width) {
    width: 700px;
    margin: 0 auto;
  }
}

.order__btn {
  width: 100%;

  @media (min-width: $tablet-width) {
    width: 230px;
  }
}

.order__required {
  margin-bottom: 30px;
  font-size: 14px;
  line-height: 16px;
  text-align: left;
  color: #444;
  order: -1;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
    font-size: 17px;
    line-height: 30px;
    order: 0;
  }
}
